		m.toLocalTime = function (UTCDateTime) {
			if (UTCDateTime !== undefined && UTCDateTime?.constructor === String) {
				let [datePart, timePart] = UTCDateTime.split(" ");
				let [year, month, day] = datePart.split("-");
				let [hours, minutes, seconds] = timePart.split(":");
				let utcDate = new Date(
					Date.UTC(year, month - 1, day, hours, minutes, seconds)
				);
					// Month from 0 base.

				let dateTimeISO = new Date(
					utcDate.getTime() + m.timezoneAddMs
				).toISOString();
					// UTC+09:00
				let [dateT, timeT] = dateTimeISO.split("T");
				return `${dateT} ${timeT.substring(0, 8)}`;
			}
			return "";
		};

