import Move from "./Move.jsx";
import Reset from "./Reset.jsx";
import Exit from "./Exit.jsx";

export default function FSGo() {
  return (
    <div id="fuzzy-search-container" className="fs-container" style={{display:'none'}}>
      <div className="fs-name">Go: Fuzzy search</div>
      <Move></Move>
      <textarea id="fuzzy-search" className="fs-input" type="text"></textarea>
      <div id="fuzzy-search-list" className="fs-list"></div>
      <Reset></Reset>
      <Exit onClick={() => m.$window.trigger({ type: 'keydown', code: 'KeyG' })}></Exit>
    </div>
  );
}