import Move from "./Move.jsx";
import Reset from "./Reset.jsx";
import Exit from "./Exit.jsx";

export default function FSTableOfRecos() {
  return (
    <div id="table-of-recos-container" className="fs-container" style={{display:'none'}}>
      <div className="fs-name">Table of Recos</div>
      <Move></Move>
      <textarea id="table-of-recos" className="fs-input" type="text"></textarea>
      <div id="table-of-recos-list" className="fs-list"></div>
      <Reset></Reset>
      <Exit onClick={() => m.$window.trigger({ type: 'keydown', code: 'KeyT' })}></Exit>
    </div>
  );
}