import $ from "jquery";
import m from "../../prime-src/prepare.js";

export default function ButtonsFloating() {
	return (
		<>
			<div id="floating-key">
				<div id="button-hideFK" className="button" onClick={() => m.hideFK()}>
					<div className="inline">[--Hid_e_: pre--]</div><div className="inline bold underline">[--Hid_e_: e--]</div><div className="inline">[--Hid_e_: post--]</div>
					</div>
				{m.myIndex === ""
				? <div className="button darkred">
					<a className="a-log-in" href="/account/log-in">
						<div className="inline">[--Log _i_n: pre--]</div><div className="inline bold underline">[--Log _i_n: i--]</div><div className="inline">[--Log _i_n: post--]</div>/<div className="inline">[--S_i_gn up: pre--]</div><div className="inline bold underline">[--S_i_gn up: i--]</div><div className="inline">[--S_i_gn up: post--]</div>
						</a>
				</div>
				: <>
					<div className={`button${m.myPage ? ` enabled` : ` cadetblue`}`}>Your id: {m.escapeOnlyTag(m.myId)}</div>
					<div className="button darkred">
						<a className="a-log-out" href="/account/log-out">
							<div className="inline">[--Log _o_ut: pre--]</div><div className="inline bold underline">[--Log _o_ut: o--]</div><div className="inline">[--Log _o_ut: post--]</div>
							</a>
					</div>
				</>}
				{!m.myPage && m.myId
				? <div className="button cadetblue">
					<a id="a-to-my-page" href={`/user/${m.myId}`}>
						<div className="inline">[--To my p_a_ge: pre--]</div><div className="inline bold underline">[--To my p_a_ge: a--]</div><div className="inline">[--To my p_a_ge: post--]</div>
						</a></div>
					: ""}
				<div className="button button-block-touch" onClick={() => m.$window.trigger({ type: 'keydown', code: 'KeyU' })}>
					<div className="inline">[--Block to_u_ch: pre--]</div><div className="inline bold underline">[--Block to_u_ch: u--]</div><div className="inline">[--Block to_u_ch: post--]</div>
				</div>
				<div className="inline point-range-result">0~10/10</div>
				<div className="point-range-con">
					<div className="point-range">
						<div className="point-range-bar"></div>
						<div className="point-range-right"></div>
						<div className="point-range-left"></div>
					</div>
				</div>
				<div className="button button-mode button-neighbors-mode" onClick={() => m.$window.trigger({ type: 'keydown', code: 'KeyB' })}>
					<div className="inline">[--Neigh_b_ors mode: pre--]</div><div className="inline bold underline">[--Neigh_b_ors mode: b--]</div><div className="inline">[--Neigh_b_ors mode: post--]</div>
				</div>
				{m.myPage
				? <>
					<div className="button button-mode button-get-recoms" onClick={() => m.$window.trigger({ type: 'keydown', code: 'KeyR' })}>
						<div className="inline">[--Get _R_ecoms: pre--]</div><div className="inline bold underline">[--Get _R_ecoms: R--]</div><div className="inline">[--Get _R_ecoms: post--]</div>
					</div>
					<div className="button button-mode button-daily-mix" onClick={() => m.$window.trigger({ type: 'keydown', code: 'KeyY' })}>
						<div className="inline">[--Dail_y_ Mix: pre--]</div><div className="inline bold underline">[--Dail_y_ Mix: y--]</div><div className="inline">[--Dail_y_ Mix: post--]</div>
					</div>
				</>
				: ``}
				<div className="button button-mode button-multireco-mode" onClick={() => m.$window.trigger({ type: 'keydown', code: 'KeyM' })}>
					<div className="inline">[--_M_ultireco mode: pre--]</div><div className="inline bold underline">[--_M_ultireco mode: M--]</div><div className="inline">[--_M_ultireco mode: post--]</div>
				</div>
				<div className="button button-Go" style={{borderRight:"none"}} onClick={() => m.$window.trigger({ type: 'keydown', code: 'KeyG' })}>
					<div className="inline">[--_G_o (FS): pre--]</div><div className="inline bold underline">[--_G_o (FS): G--]</div><div className="inline">[--_G_o (FS): post--]</div>
				</div>
				<div className="button button-ToR" onClick={() => m.$window.trigger({ type: 'keydown', code: 'KeyT' })}>
					<div className="inline">[--_T_ofR: pre--]</div><div className="inline bold underline">[--_T_ofR: T--]</div><div className="inline">[--_T_ofR: post--]</div>
				</div>
				<div className="button darkgoldenrod" onClick={() => m.$window.trigger({ type: 'keydown', code: 'KeyZ' })}>
					<div className="inline">[--To List Play(_Z_): pre--]</div><div className="inline bold underline">[--To List Play(_Z_): Z--]</div><div className="inline">[--To List Play(_Z_): post--]</div>
				</div>
				<div className="button darkgoldenrod" onClick={() => m.$window.trigger({ type: 'keydown', code: 'KeyX' })}>
					<div className="inline">[--To Contents(_X_): pre--]</div><div className="inline bold underline">[--To Contents(_X_): X--]</div><div className="inline">[--To Contents(_X_): post--]</div>
				</div>
				<div className="button darkgoldenrod" onClick={() => m.$window.trigger({ type: 'keydown', code: 'KeyD' })}>
					<div className="inline">[--Backwar_d_: pre--]</div><div className="inline bold underline">[--Backwar_d_: d--]</div><div className="inline">[--Backwar_d_: post--]</div>
				</div>
				<div className="button darkgoldenrod" onClick={() => m.$window.trigger({ type: 'keydown', code: 'KeyF' })}>
					<div className="inline">[--_F_orward: pre--]</div><div className="inline bold underline">[--_F_orward: F--]</div><div className="inline">[--_F_orward: post--]</div>
				</div>
				<div className="button button-goto-cats" onClick={() => m.$window.trigger({ type: 'keydown', code: 'KeyC' })}>
					<div className="inline">[--Go to _C_ats: pre--]</div><div className="inline bold underline">[--Go to _C_ats: C--]</div><div className="inline">[--Go to _C_ats: post--]</div>
				</div>
				<div className="button button-new-reco" onClick={() => m.$window.trigger({ type: 'keydown', code: 'KeyN' })}>
					<div className="inline">[--_N_ew reco: pre--]</div><div className="inline bold underline">[--_N_ew reco: N--]</div><div className="inline">[--_N_ew reco: post--]</div>
				</div>
				<div id="SNS-floating"><img className="SNS-img" src="/CDN/link.png" onClick={() => m.shareCurrentPage('link')}/><img className="SNS-img" src="/CDN/icon-Tag.png" onClick={() => m.shareCurrentPage('tag')}/><img className="SNS-img" src="/CDN/icon-Recoeve.png" onClick={() => m.shareCurrentPage('recoeve')}/><img className="SNS-img" src="/CDN/icon-X.png" onClick={() => m.shareCurrentPage('X')}/><img className="SNS-img" src="/CDN/icon-Facebook.png" onClick={() => m.shareCurrentPage('facebook')}/><img className="SNS-img" src="/CDN/icon-Kakao.png" onClick={() => m.shareCurrentPage('kakao')}/><img className="SNS-img" src="/CDN/icon-Whatsapp.png" onClick={() => m.shareCurrentPage('whatsapp')}/></div>
			</div>
			<div className="button" id="toggle-floating-key" onClick={() => m.toggleFK()}>▲</div>
		</>
	);
}
