import $ from 'jquery';
import m from "../../prime-src/prepare.js";
import { useEffect } from 'react';

export default function Sidebar({ userCatList, opened, isChangingOrder, onChangeOrder }) {
	useEffect(() => {
		m.$change_catList_order = $("#change-catList-order");
		m.$change_catList_order_ok = $("#change-catList-order-ok");
		m.$change_catList_order_cancel = $("#change-catList-order-cancel");
	}, []);

	useEffect(() => {
		try {
			m.$change_catList_order_cancel.trigger("click.change-catList-order");
		} catch (err) {
			console.error(err);
		}
	}, [userCatList]);

	return (
		<aside id="sidebar" count={opened.count}>
			<div id="user-noti"></div>
			<div id="catList"></div>
			<div id="catList-result"></div>
			<div className="right">
				<div className="button" style={{display: !isChangingOrder ? 'inline-block' : 'none'}} id="change-catList-order" onClick={() => {
					onChangeOrder(true);
				}}>[--Change orders--]</div>
				<div className="button" style={{display: isChangingOrder ? 'inline-block' : 'none'}} id="change-catList-order-ok" onClick={() => {
					onChangeOrder(false);
				}}>[--OK--]</div>
				<div className="button" style={{display: isChangingOrder ? 'inline-block' : 'none'}} id="change-catList-order-cancel" onClick={() => {
					onChangeOrder(false);
				}}>[--Cancel--]</div>
			</div>
		</aside>
	);
}