import Move from "./Move.jsx";
import Reset from "./Reset.jsx";
import Exit from "./Exit.jsx";

export default function FSTimezone() {
  return (
    <div id="search-timezone-container" className="fs-container" style={{display:'none'}}>
      <div className="fs-name">Fuzzy search Timezone</div>
      <Move></Move>
      <textarea id="search-timezone" className="fs-input" type="text"></textarea>
      <div id="search-timezone-list" className="fs-list"></div>
      <Reset></Reset>
      <Exit onClick={() => m.$choose_timezone.trigger('click')}></Exit>
    </div>
  );
}
