export default function RightTop() {
  return (
    <>
      <div className="side2" id="right-top-log-in">
        <div className="button" id="my-id"></div>
        <div className="button" id="choose-lang">▼ Language/언어/語言</div>
        <div className="button" id="choose-timezone">▼ Timezone/시간대/時區</div>
        {m.myIndex === "" ?
        <div className="button darkred"><a className="a-log-in" href="/account/log-in">[--Log _i_n: pre--]<span className="bold underline">[--Log _i_n: i--]</span>[--Log _i_n: post--] / [--S_i_gn up--]</a></div> :
        <>
          <div className="button darkred"><a className="a-log-out" href="/account/log-out">[--Log _o_ut: pre--]<span className="bold underline">[--Log _o_ut: o--]</span>[--Log _o_ut: post--]</a></div>
          <div className="button darkred"><a className="a-log-out-from-all" href="/account/log-out-from-all">[--Log out from all--]</a></div>
        </>}
      </div>
      <div className="cBoth"></div>
    </>
  );
}