import $ from 'jquery';
import m from "../../prime-src/prepare.js";
import Reset from './Reset.jsx';
import Exit from './Exit.jsx';

export default function FormNewReco() {
  return (
    <div id="new-reco">
      <form>
        <label for="input-uri">URI <span className="desc">([--required--])</span></label>
        <textarea className="single-line" id="input-uri" name="uri" placeholder="[--placeholder URI--]" type="text" tabindex="1"></textarea>
        <Reset onClick={(event) => {
          m.$input_uri[0].value = "";
          m.$input_uri.trigger("focus");
          m.$input_uri.trigger("keyup");
        }}></Reset>
        <label for="input-title">[--Title--] <span className="desc"><code>Ctrl+Enter</code> to Reco/Edit
            directly</span></label>
        <div id="def-titles"></div>
        <textarea className="single-line" id="input-title" name="title" placeholder="[--placeholder Title--]" type="text" tabindex="2"></textarea>
        <div id="lyrics-search" className="button fRight">Lyrics Search in genius.com</div>
        <div className="cBoth"></div>
        <label for="input-cats">[--Categories--]</label>
        <div id="def-cats">
          <div className="def-cat replace-cat">[--Indifferent--]</div>
          <div className="def-cat replace-cat">[--Later--]</div>
          <div className="def-cat replace-cat">[--Stashed--]</div>
          <div id="add-cat" className="def-cat add-txt">;</div>
          <div id="sub-cat" className="def-cat add-txt">--</div>
          <div id="delete-cat" className="def-cat delete-cat">[--Delete--]</div>
        </div>
        <textarea className="single-line" id="input-cats" name="cats" placeholder="[--placeholder Category--]" type="text" tabindex="3"></textarea>
        <div id="cat-fs-container" className="fs-container" style={{display:'none'}}>
          <div id="cat-fs-list" className="fs-list"></div>
          <Exit id="exit-cat-fs-container" onClick={(e) => $(e.target).parents(".fs-container").hide()}/>
        </div>
        <div className="buttons">
          <div className="button fLeft button-close" tabindex="4" onClick={() => m.closeNewReco()}>[--Close--]</div>
          <div className="button fLeft button-back" tabindex="5">[--Back--]</div>
          <div className="button button-reco" tabindex="6">[--Reco--]</div>
          <div className="button button-edit" tabindex="7">[--Edit--]</div>
          <div className="button button-del" tabindex="8" style={{background:'#ff7600'}}>[--Del--]</div>
          <div className="cBoth"></div>
        </div>
        <div style={{margin:'.5em 0'}}>
          <label className="label-input-val" for="input-val">[--Points--]</label>
          <div className="input-val-stars"></div>
          <span className="upDown input-val-up">&gt;</span>
          <span className="upDown input-val-down">&lt;</span>
          <input className="input-val" name="val" placeholder="(ex: 7.5/10)" type="text" tabindex="9" />
          <div className="cBoth"></div>
        </div>
        <div id="error-msg" className="cBoth"></div>
        <div id="show-URI"></div>
        <label for="input-desc">[--Description--]</label>
        <div id="def-cmds">
          <div id="preview-desc">[--Preview--]</div>
          <div className="def-cmd">#related</div>
          <div className="def-cmd">#lyrics</div>
          <div id="formatting-lyrics">[--Formatting the lyrics--] (mainly from genius.com)</div>
          <div className="def-cmd">#dictionary</div>
        </div>
        <div id="def-descs"></div>
        <textarea className="multi-line" id="input-desc" name="desc" placeholder="[--placeholder Description--]" tabindex="10"></textarea>
        <div id="previewed-desc"></div>
        <label for="input-cmt">[--Comment--]</label>
        <textarea className="multi-line" id="input-cmt" name="cmt" placeholder="[--placeholder Comment--]" tabindex="11"></textarea>
        <div id="my-reco"></div>
        <Exit id="exit-new-reco" style={{ margin: '-1em -.2em 0 0' }} onClick={(e) => m.$button_close.trigger("click")}/>
        <div className="buttons">
          <div className="button fLeft button-close" tabindex="12" onClick={() => m.closeNewReco()}>[--Close--]</div>
          <div className="button fLeft button-back" tabindex="13">[--Back--]</div>
          <div className="button button-reco" tabindex="14">[--Reco--]</div>
          <div className="button button-edit" tabindex="15">[--Edit--]</div>
          <div className="button button-del" tabindex="16" style={{background:'#ff7600'}}>[--Del--]</div>
          <div className="cBoth"></div>
        </div>
        <div style={{margin:'.5em 0'}}>
          <label className="label-input-val" for="input-val">[--Points--]</label>
          <div className="input-val-stars"></div>
          <span className="upDown input-val-up">&gt;</span>
          <span className="upDown input-val-down">&lt;</span>
          <input className="input-val" name="val" placeholder="(ex: 7.5/10)" type="text" tabindex="17" />
          <div className="cBoth"></div>
        </div>
      </form>
    </div>
  );
}
