import $ from 'jquery';
import m from "../../prime-src/prepare";
import SidebarDragger from "./SidebarDragger.jsx";
import Sidebar from "./Sidebar.jsx";
import { useState, useEffect } from "react";
import { useImmer } from 'use-immer';

export default function SidebarCon({userCatList}) {
  const [opened, updateOpened] = useImmer({value: true, count: 0});
  const [isChangingOrder, setIsChangingOrder] = useState(false);

  async function initialEffect() {
    m.$sidebar_dragger = $("#sidebar-dragger");
    m.$sidebar_toggle = $("#sidebar-toggle");
    m.$sidebar_dragger_in = $("#sidebar-dragger-in");
    m.$sidebar = $("#sidebar");
    m.$catList = $("#catList");
    m.$catList.on("mouseup.move-cat", (e) => {
			try {
				m.triggerOpenCat(e);
			} catch (err) {
				// console.error(err);
        // ! clicking blank space is normal activities.
			}
			return false;
		});
    m.$catList_result = $("#catList-result");
    if (window.innerWidth < 700) {
      updateOpened(draft => { draft.value = false; });
    }
    await m.strCatListToJSON(m.unescapeHTML(userCatList.text), m.catList);
		await m.catListToHTML();
			// ! catList 는 SideBar 에다 그리는 용도와 m.fsGotoCats 로만 쓰여서 FS 와 관련 없음. 그래서 m.myCatList 는 update 불필요. 단 m.myCatListHTMLEscaped 는 m.myFSCatList 를 위해 업데이트 필요!!!
		await m.strCatListToJSON(m.unescapeHTML(userCatList.text.trim()+"\n"+m.catListHTMLEscaped.trim()+"\n"+m.kipidCatListHTMLEscaped.trim()), m.myFSCatList);
    m.$sidebar_dragger_in.on("mousedown.sd touchstart.sd", function (e) {
      let touch0 = e.type === "touchstart" ? e.originalEvent.touches[0] : e;
      let relativeX = touch0.clientX - Math.round(m.$sidebar_dragger.offset().left) + m.$window.scrollLeft();
      m.$html.on("mousemove.sd touchmove.sd", function (e) {
        window.getSelection().removeAllRanges();
        e.preventDefault();
        e.stopPropagation();
        let touch0 = e.type === "touchmove" ? e.originalEvent.touches[0] : e;
        m.$sidebar.css({
          marginLeft: -m.$sidebar.outerWidth() + touch0.clientX - relativeX,
        });
        m.$sidebar_dragger.css({ left: touch0.clientX - relativeX });
        m.$html.on("mouseup.sd touchend.sd", function () {
          m.$html.off("mouseup.sd touchend.sd mousemove.sd touchmove.sd");
          if (touch0.clientX - relativeX > m.$sidebar.outerWidth() / 2) {
            m.$sidebar.css({ marginLeft: 0 });
            m.$sidebar_dragger.css({ left: m.$sidebar.outerWidth() });
            updateOpened(draft => { draft.value = true; draft.count = draft.count+1; });
          } else {
            m.$sidebar.css({ marginLeft: -m.$sidebar.outerWidth() });
            m.$sidebar_dragger.css({ left: 0 });
            updateOpened(draft => { draft.value = false; draft.count = draft.count+1; });
          }
        });
      });
    });
    m.$window.on("resize.sidebar", function (e) {
      updateOpened(draft => {
        draft.count = draft.count + 1;
        if (window.innerWidth < 700) {
          draft.value = false;
          m.$sidebar.css({ marginLeft: -m.$sidebar.outerWidth() });
          m.$sidebar_dragger.css({ left: 0 });
        } else {
          draft.value = true;
          m.$sidebar.css({ marginLeft: 0 });
          m.$sidebar_dragger.css({ left: m.$sidebar.outerWidth() });
        }
      });
    });
  }

  useEffect(initialEffect, []);

  return (
    <>
      <SidebarDragger opened={opened} updateOpened={updateOpened}></SidebarDragger>
      <Sidebar userCatList={userCatList} opened={opened} isChangingOrder={isChangingOrder} onChangeOrder={setIsChangingOrder}></Sidebar>
    </>
  );
}
