import Move from "./Move.jsx";
import Reset from "./Reset.jsx";
import Exit from "./Exit.jsx";

export default function FSGotoCats() {
  return (
    <div id="goto-cats-container" className="fs-container" style={{display:'none'}}>
      <div className="fs-name">Go to Cats</div>
      <Move></Move>
      <textarea id="goto-cats" className="fs-input" type="text"></textarea>
      <div id="goto-cats-list" className="fs-list"></div>
      <Reset></Reset>
      <Exit onClick={() => m.$window.trigger({ type: 'keydown', code: 'KeyC' })}></Exit>
    </div>
  );
}