export default function ButtonsHead() {
  return (
    <header className="side2" id="head">
      <div className="button button-Go" onClick={() => m.$window.trigger({ type: 'keydown', code: 'KeyG' })}>[--Fuzzy Search--] [--_G_o (FS): pre--]<span className="bold underline">[--_G_o (FS): G--]</span>[--_G_o (FS): post--]</div>
      <div className="button button-goto-cats" onClick={() => m.$window.trigger({ type: 'keydown', code: 'KeyC' })}>[--Go to _C_ats: pre--]<span className="bold underline">[--Go to _C_ats: C--]</span>[--Go to _C_ats: post--]</div>
      <div className="button button-mode button-neighbors-mode" onClick={() => m.$window.trigger({ type: 'keydown', code: 'KeyB' })}>[--Neigh_b_ors mode: pre--]<span className="bold underline">[--Neigh_b_ors mode: b--]</span>[--Neigh_b_ors mode: post--]</div>
      <div className="button button-mode button-multireco-mode" onClick={() => m.$window.trigger({ type: 'keydown', code: 'KeyM' })}>[--_M_ultireco mode: pre--]<span className="bold underline">[--_M_ultireco mode: M--]</span>[--_M_ultireco mode: post--]</div>
      <div className="button button-new-reco" onClick={() => m.$window.trigger({ type: 'keydown', code: 'KeyN' })}>[--_N_ew reco: pre--]<span className="bold underline">[--_N_ew reco: N--]</span>[--_N_ew reco: post--]</div>
    </header>
  );
}
