import $ from 'jquery';

export default function SidebarDragger({ opened, updateOpened }) {
  return (
    <div id="sidebar-dragger" count={opened.count} style={{left: opened.value ? ($("#sidebar").length ? $("#sidebar").outerWidth() : window.innerWidth >= 900 ? '20em' : '15em') : 0}}>
      <svg id="sidebar-toggle" onClick={() => {
        updateOpened(o => {
          if (o.value) {
            m.$sidebar.css({"margin-left":`${-m.$sidebar.outerWidth()}px`});
          } else {
            m.$sidebar.css({"margin-left":0});
          }
          return {...o, value:!o.value, count:o.count+1}
        });
      }}>
        {opened.value ?
        <g>
          <line x1="20%" y1="50%" x2="80%" y2="50%"></line>
          <line x1="20%" y1="50%" x2="50%" y2="20%"></line>
          <line x1="20%" y1="50%" x2="50%" y2="80%"></line>
        </g> :
        <g>
          <line x1="20%" y1="50%" x2="80%" y2="50%"></line>
          <line x1="80%" y1="50%" x2="50%" y2="20%"></line>
          <line x1="80%" y1="50%" x2="50%" y2="80%"></line>
        </g>}
      </svg>
      <div id="sidebar-dragger-in"></div>
    </div>
  );
}